import React, {useEffect, useState} from 'react';
import {Button, Col, Container, Form, Row} from "react-bootstrap";
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import Calendar from "./Calendar";
import axios from "axios";


const initialFormData = Object.freeze({
    userTask: "",
    userDesc: "",
    userPriority: "",
    startDate: "",
    startTime: "",
    endDate: "",
    endTime: ""
});

const TaskView = (props) => {
    const [nameTask, setNameTask] = useState('');
    const [formData, updateFormData] = useState(initialFormData);
    const handleChange = (e) => {
        if (!e.value && !e.length) {
            setNameTask(e.target.value);
            updateFormData({
                ...formData,
                [e.target.name]: e.target.value.trim()
            })
        } else if (e.length) {
            if (e.length == 2) {
                updateFormData({
                    ...formData,
                    ["startDate"]: `${e[0].year}-${e[0]['month']["number"]}-${e[0].day}`,
                    ["startTime"]: `${e[0].hour}:${e[0].minute}`,
                    ["endDate"]: `${e[1].year}-${e[1]['month']["number"]}-${e[1].day}`,
                    ["endTime"]: `${e[1].hour}:${e[1].minute}`,
                })
            } else {
                updateFormData({
                    ...formData,
                    ["startDate"]: `${e[0].year}-${e[0]['month']["number"]}-${e[0].day}`,
                    ["startTime"]: `${e[0].hour}:${e[0].minute}`
                })
            }

        } else {
            updateFormData({
                ...formData,
                ["userPriority"]: e.value
            })
        }
    }
    useEffect(() => {
        axios({
            method: "POST",
            url: "http://127.0.0.1:8000/api/to_do_list/",
            task: initialFormData.userTask,
            description: initialFormData.userDesc,
            start_data: initialFormData.startDate,
            end_data: initialFormData.endDate,
            start_time: initialFormData.startTime,
            end_time: initialFormData.endTime,
            priority: initialFormData.userPriority,
        }).then(response => {
            console.log(response)
        })
            .catch()(function (error) {
                console.log(error);
            });
    })
    const handleSubmit = (e) => {
        e.preventDefault()
        console.log(formData)
        props.func()
    };
    const options = [
        'No priority', 'Priority 1', 'Priority 2', 'Priority 3', 'Priority 4'
    ];

    return (
        <Container className="mb-5">
            <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Control type="text" placeholder="Task's name" name="userTask"
                                  onChange={handleChange}/>
                    <Form.Control as="textarea" rows={3} placeholder="Task's description" name="userDesc"
                                  onChange={handleChange}/>
                    <Row>
                        <Col className='mt-2' sm={12} lg={6}><Dropdown options={options} value={options[0]}
                                                                       placeholder="Select an option"
                                                                       name="userPriority"
                                                                       onChange={handleChange}/></Col>
                        <Col className='mt-3' sm={12} lg={6}>
                            <Calendar handler={handleChange}></Calendar>
                        </Col>
                    </Row>
                </Form.Group>
            </Form>

            <Row className="mt-2">
                <Col xs={6} sm={2} lg={1}><Button variant="dark" disabled={!nameTask} size="sm"
                                                  onClick={handleSubmit}>ADD</Button></Col>
                <Col xs={6} sm={2} lg={1}><Button variant="dark" size="sm" onClick={() => {
                    props.func()
                }}>CANCEL</Button></Col>
            </Row>
        </Container>
    );
};
export default TaskView